export default {
	data() {
		return {
			toplist:[],
			isactive:1,
			sh:false,
			h:'',
			h1:'',
			h2:'',
			h3:'',
			phoneHeight:'',
			huadong:1,
			iscollect:0,//收藏图标
			iscollecting:false,//收藏框
			isuncollecting:false,//取消收藏框
			ischeck:false,//查看 全部图片
			userinfo:[],
			id:0,//体验家id
			user_id:0,
			homelist:[],//体验家内容
			commentsval:[],//评论内容
		}
	},
	onReady(){
		uni.getSystemInfo({
			 success:  (res) => {   
				this.h3 = res.windowHeight - uni.upx2px(25)
			}
		}),
		this.getNodesInfo(),
		uni.getSystemInfo({
			success: (res) => {
				this.phoneHeight = res.windowHeight;
			}
		});
	},
	onLoad(options) {
		options.id=5
		this.id = options.id
		// console.log(options)
		uni.getStorage({
			key: 'userinfo',
			success:(res)=>{
				this.userinfo = res.data
				this.user_id = res.data.id
			}
		});	
		this.get_defs()
		this.comments()
	},
	onPageScroll(Object){
		if(Object.scrollTop >= this.h){
			this.sh = true
		}else if(Object.scrollTop < this.h){
			this.sh = false
		}
	},
	
	
	methods: {
		getimgurl(image){
			return this.common.websiteUrl+image
		},
		huadongs(e){
			var c = e.detail.current
			this.huadong = c + 1
			console.log(e)
		},
		get_defs(){
			let that = this
			uni.request({
				url:that.common.websiteUrl+"experhome_home_homedef",
				header:{"user-token":"6a109faf305513d443337ddb1ad4cb9b"},
				method:"post",
				data:{
						'user_id':that.user_id,
						'id':that.id
					},
				success: (res) => {
					if(res.data.code==200){
						that.homelist =res.data.data
						that.iscollect = res.data.data.coll_is
						this.$nextTick(() => {
							this.$refs.uReadMore.init();
						})
					}else{
						that.common.network()
					}
					console.log(res.data.data)
				},fail() {
					that.common.network()
				}
			});
		},
		comments(){
			let  that = this
			uni.request({
				url:that.common.websiteUrl+"experhome_owners_comments",
				header:{"user-token":"6a109faf305513d443337ddb1ad4cb9b"},
				method:"post",
				data:{
						'exper_id':that.id,
						// 'id':that.id
					},
				success: (res) => {
					if(res.data.code==200){
						that.commentsval =res.data.data
					}
					console.log(res.data.data)
				},fail() {
					that.common.network()
				}
			});
		},
		back(){
			uni.navigateBack({
			    delta: 1
			});
		},
		nav(index){
			this.isactive = index;
			this.h1 = "po"+index;
		},
		collect(){
			// 收藏
			let that =this
			uni.getStorage({
				key: 'userinfo',
				success:(res)=>{
					uni.request({
						url:that.common.websiteUrl+"user_coll",
						header:{"user-token":"6a109faf305513d443337ddb1ad4cb9b"},
						method:"post",
						data:{
								'user_id':that.user_id,
								'pid':that.id,
								'type':3,
								'coll_is':that.iscollect
							},
						success: (res) => {
							if(res.data.code==200){
								if(that.iscollect == 1){
									// 取消收藏框
									that.isuncollecting = true
									that.iscollect = 0
									setTimeout(() => {
										that.isuncollecting = false
									}, 1000);
								}else{
									// 收藏框
									that.iscollecting = true
									that.iscollect = 1
									setTimeout(() => {
										that.iscollecting = false
									}, 1000);
								}
							}else{
								that.common.network()
							}
							console.log(res.data.data)
						},fail() {
							that.common.network()
						}
					});
				},fail() {
					uni.navigateTo({
						url:'../login/login'
					})
				}
			});	
		},
			
		
		check(){
			// 查看全部图片
			this.ischeck = true
		},
		checkback(){
			// 返回查看全部图片
			this.ischeck = false
		},
		address(){
			uni.navigateTo({
			    url: '../orderaddress/orderaddress'
			});
		},
		makeapp(){
			uni.navigateTo({
			    url: '../orderexperience/orderexperience?id='+this.id
			});
		},
		gopro(id){
			// return console.log('处罚')
			uni.navigateTo({
			    url: '../productdetail/productdetail?id='+id
			});
		},
		allassess(){
			uni.navigateTo({
			    url: '../experienceassess/experienceassess?id='+this.id
			});
		},
		scrolltolower(){
			setTimeout(()=>{
				this.isactive=3
			},10)
		},
		scroll(e){
			let scrolltop = e.target.scrollTop;
			console.log(scrolltop);
			let h11 = this.toplist[0]-380;
			let h12 = this.toplist[1]-190;
			let h13 = this.toplist[2]-250;
			if(scrolltop>=h12&&scrolltop<h13){
				this.isactive = 2
			}else if(scrolltop>=h13){
				this.isactive = 3
			}else{
				this.isactive = 1
			}
		},
		getNodesInfo(){
			let info3 = uni.createSelectorQuery().in(this);
			info3.selectAll('.tit').boundingClientRect().exec((res)=>{
				console.log(res)
				let nodes = res[0];
				let rel=[];
				nodes.map(item=>{
					rel.push(item.top)
				})
				this.toplist = rel
			})
			let info1 = uni.createSelectorQuery().select(".menu");
			info1.boundingClientRect().exec((res)=>{
				this.h = res[0].height-20;
			})
			let info2 = uni.createSelectorQuery().select(".nav");
			info2.boundingClientRect().exec((res)=>{
				this.h2 = res[0].height;
			})
		}
	}
}
